
/*
  Main App style
*/
.appStyle {
  background-image: url('./sources/repBack.jpg');
  background-repeat: repeat;
  background-attachment: fixed;
}

/*
  Header style
*/
.headerStyle {
  width: 100%;
  height: 100%;
  align-items: center;
  text-align: center;
  padding: 10px;
  margin: 0;
  background-image: url('./sources/foulard.jpg');
  background-position: center;
  background-size: cover;
}

.mainTitle {
  color: white;
  text-shadow: 2px 2px black;
}

/*
  Charging page style
*/
.chargeStyle {
  text-align: center;
  color: green;
}

/*
  Card style
*/
.cardStyle {
  margin: 20px 10px;
  width: auto;
  border-radius: 5px
}

.titleStyle {
  margin: 6px 0px 8px 0px
}

@media only screen and (min-width: 440px) {
  .cardStyle {
    width: 400px;
    display: inline-block;
  }
}
